.post {
    background-color: white;
    padding: 15px;
    margin-bottom: 10px;
    border-radius: 10px;
}

.post__header {
    display: flex;
    margin-bottom: 10px;
}

.post__info {
    margin-left: 10px;
}

.post__info>p {
    font-size: 12px;
    color: gray;
}

.post__body {
    overflow-wrap: anywhere;
}

.post__buttons {
    display: flex;
    justify-content: space-evenly;
}

.post__header>img {
    width: 50px;
    height: 50px;
    border-radius: 50%;
}