.widget {
    position: sticky;
    top: 80px;
    flex: 0.3;
    background-color: white;
    border-radius: 10px;
    border: 1px solid lightgray;
    height: fit-content;
    padding-bottom: 10px;
}

.widget__header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px;
}

.widget__header>h2 {
    font-size: 16px;
    font-weight: 400;
}

.widget__article {

    display: flex;
    padding: 10px;
    cursor: pointer;
}

.widget__article:hover {

    background-color: whitesmoke;
}

.widget__articleLeft {
    color: #0177b7;
    margin-right: 5px;
}

.widget__articleLeft>.MuiSvgIcon-root {
    font-size: 50px;
}

.widget__articleRight {
    flex: 1;
}

.widget__articleRight>h4 {
    font-size: 14px;
}

.widget__articleRight>p {
    font-size: 12px;
    color: gray;
}

.widget__button__follow {
    display: flex;
    color: gray;
    border: 2px solid gray;
    border-radius: 30px;
    margin-top: 5px;
    margin-right: 120px;
    padding-left: 0.5rem;
    padding-right: 1rem;
    padding-top: 0.2rem;
    padding-top: 0.2rem;
    font-weight: 600;
}

@media (max-width: 850px) {
    .widget {
        display: none;
    }
}

@media (max-width: 1000px) {
    .widget {
        flex: 0.2;
    }
}