.inputOption {
    display: flex;
    align-items: center;
    margin-top: 15px;
    color: gray;
    cursor: pointer;
}

.inputOption:hover {
    background-color: whitesmoke;
    border-radius: 10px;
}

.inputOption>h4 {
    margin-left: 10px;
    font-weight: 500;
}

.inputOption>.MuiSvgIcon-root {
    width: 20px;
}