.feed {
    flex: 0.6;
    margin: 20px 20px;
}

.feed__inputContainer {
    background-color: white;
    padding: 10px;
    padding-bottom: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
}

.feed__input {
    border: 1px solid lightgray;
    border-radius: 30px;
    display: flex;
    margin-left: 70px;
    padding: 10px;
    color: gray;
    padding-left: 15px;
}

.feed__input>form {
    display: flex;
    width: 50%;
}

.feed__input>form>input {
    border: none;
    flex: 1;
    margin-left: 10px;
    outline-width: 0;
    font-weight: 600;
}

.feed__input>form>button {
    display: none;
}

.feed__inputOptions {
    display: flex;
    justify-content: space-evenly;
}

@media (max-width: 720px) {
    .feed {
        min-width: 90%;
    }

}

@media (max-width: 850px) {
    .feed {
        flex: 0.7;
    }
}